/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 0;
    width: auto;
    height: 40px;
    margin: 0;
    padding: 12px 18px;
    color: $default-text-color;
    font-size: 11px;
    font-weight: 500;
    text-transform: uppercase;
    line-height: 25px;
    letter-spacing: 0.12em;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    @include eltdfTransform(translateZ(0));
}

@mixin eltdfWooCommerceSaleStyle() {
    right: 0;
    //background-color: #fff;
}

@mixin eltdfWooCommerceOutOfStockStyle() {
    right: 0;
    //background-color: #fff;
}

@mixin eltdfWooCommerceNewProductStyle() {
    right: 0;
    //background-color: #fff;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 2px 0 0;
    line-height: 1;
    color: $first-main-color;
    
    * {
        font-size: 11px;
        text-transform: uppercase;
        font-weight: 500;
        letter-spacing: 0.12em;
    }
    
    del {
        margin: 0 8px 0 0;
        color: $first-main-color;
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 12px;
    line-height: 1;
    letter-spacing: 2px;
    color: $default-button-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin eltdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
        display: block;
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
            display: block;
        }
    }
}

@mixin eltdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e031";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\e033\e033\e033\e033\e033";
        position: absolute;
        right: 0;
        bottom: 9px;
        display: block;
        @extend %star-ratings;
    }
    
    @supports (-ms-ime-align: auto) {
        &.active{
	        &:after{
                width: 70px;
		        //left: -42px;
	        }
        }
    }
	
	@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
		&.active{
			&:after{
				width: 70px;
				//left: -42px;
			}
		}
	}
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */