/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.eltdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .eltdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .eltdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;

        &:hover {
            color: $default-heading-color;

            .eltdf-cart-icon {

                .eltdf-cart-number {
                    background-color: $first-main-color;
                }
            }
        }

        .eltdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;

            .eltdf-cart-number {
                display: block;
                position: absolute;
                top: 9px;
                left: -6px;
                height: 16px;
                width: 16px;
                line-height: 16px!important;
                border-radius: 50%;
                font-size: 8px;
                text-align: center;
                background-color: #cca06f;
                color: #fff;
                @include eltdfTransition(background-color .2s ease-out);
            }
        }

        &.eltdf-header-cart-icon-pack {
            
            .eltdf-cart-icon {
                font-size: 14px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.eltdf-header-cart-svg-path {
            
            .eltdf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .eltdf-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;

        .eltdf-cart-icon {

            .eltdf-cart-number {
                background-color: $header-dark-hover-color;
            }
        }
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-light-color;

    .eltdf-cart-icon {

        .eltdf-cart-number {
            color: $header-dark-color;
            background-color: $header-light-hover-color;
        }
    }

    &:hover {
        color: $header-light-hover-color;

        .eltdf-cart-icon {

            .eltdf-cart-number {
                color: $header-dark-color;
                background-color: $header-light-hover-color;
            }
        }
    }
}

.eltdf-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 285px;
    background-color: #010513 ;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include eltdfTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 30px 28px 30px 27px;
        padding: 0;

        li {
            @include eltdfTableLayout();
            padding: 12px 0;
            box-sizing: border-box;

            &.eltdf-empty-cart {
                color: #bababa;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .eltdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 76px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .eltdf-item-info-holder {
        @include eltdfTableCellLayout();
        vertical-align: top;
        padding-left: 15px;

        .eltdf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            color: #fff;
            box-sizing: border-box;
        }

        .remove {
            position: absolute;
            top: 7px;
            right: -3px;
	        font-size: 15px;
	        line-height: 1;
            color: $additional-text-color;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }

            &:hover {
                color: $first-main-color;
            }
        }

        .eltdf-quantity {
            display: block;
            margin: 2px 0 0;
            color: #bababa;
            font-size: 12px;
            line-height: 1;
        }

        .amount {
            display: block;
            margin: 1px 0 0;
            font-size: 11px;
            font-weight: 500;
            letter-spacing: 2px;
            color: $first-main-color;
        }
    }

    .eltdf-cart-bottom {
        padding: 14px 0 0;

        .eltdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 19px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: #fff;
            }

            .eltdf-total {
                font-family: $default-heading-font;
                font-weight: 600;
                font-size: 21px;
                line-height: 1.103em;
            }

            .eltdf-total-amount {
                text-align: right;
                font-size: 11px;
                font-weight: 500;
                letter-spacing: 2px;
                color: $first-main-color;
            }
        }

        .eltdf-view-cart {
            @include eltdfRelativeHolderLayout();
            width: auto;
            height: 42px;
            line-height: 42px;
            padding: 0 18px;
            font-size: 10px;
            font-weight: 500;
	        letter-spacing: 2px;
            text-transform: uppercase;
            color: #fff;
            background-color: $default-button-color;
            border: 1px solid $default-button-color;
            text-align: center;
            margin-right: 5px;
            box-sizing: border-box;
            float: left;
            @include eltdfTransition(color .2s ease-out, background-color .2s ease-out);

            span {
                display: block;
            }

            &:hover {
                color: #fff;
                border: 1px solid #d9ad7c;
                background-color: #d9ad7c;
            }
        }

        .eltdf-checkout {
            @include eltdfRelativeHolderLayout();
            width: auto;
            height: 42px;
            line-height: 42px;
            padding: 0 17px;
            font-size: 10px;
            font-weight: 500;
            letter-spacing: 2px;
            text-transform: uppercase;
            color: #fff;
            background-color: transparent;
            border: 1px solid $first-main-color;
            text-align: center;
            box-sizing: border-box;
            float: right;
            @include eltdfTransition(color .2s ease-out, background-color .2s ease-out);
    
            @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
                padding: 0 16px;
            }
    
            @supports (-ms-ime-align: auto) {
                padding: 0 16px;
            }
            
            span {
                display: block;
            }
    
            > span[class*='eltdf-line'] {
                position: absolute;
                background-color: $default-button-color;
                visibility: hidden;
                @include eltdfTransition(all .1s);
            }
    
            .eltdf-line-1 {
                top: -1px;
                left: -1px;
                height: 3px;
                width: 100%;
                @include eltdfTransform(scaleY(0));
            }
    
            .eltdf-line-2 {
                top: -1px;
                right: -1px;
                height: 100%;
                width: 3px;
                @include eltdfTransform(scaleX(0));
            }
    
            .eltdf-line-3 {
                bottom: -1px;
                right: -1px;
                height: 3px;
                width: 100%;
                @include eltdfTransform(scaleY(0));
            }
    
            .eltdf-line-4 {
                bottom: -1px;
                left: -1px;
                height: 100%;
                width: 3px;
                @include eltdfTransform(scaleX(0));
            }
    
            &:hover {
                > span[class*='eltdf-line'] {
                    visibility: visible;
                }
        
                .eltdf-line-1,
                .eltdf-line-3 {
                    @include eltdfTransform(scaleY(1));
                    @include eltdfTransition(all .3s cubic-bezier(0.47, 0.07, 0.23, 0.99));
                }
        
                .eltdf-line-2,
                .eltdf-line-4 {
                    @include eltdfTransform(scaleX(1));
                    @include eltdfTransition(all .3s cubic-bezier(0.47, 0.07, 0.23, 0.99));
                }
            }
        }
    }
}

.eltdf-vertical-menu-area {

    .eltdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */