/* ==========================================================================
   YITH WishList styles - begin
   ========================================================================== */

#yith-wcwl-popup-message {
    position: absolute;
    margin: 0!important;
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    @include eltdfTransform(translate(-50%,-50%));
    border: 1px solid #ebebeb;
    border-radius: 0;
    padding: 0 21px;
    line-height: 35px;

    #yith-wcwl-message {
        font-size: 14px;
        font-weight: 600;
        letter-spacing: 0.1em;

        &:before {
            position: relative;
            font-family: 'FontAwesome';
            content: "W";
            top: 0;
            right: 7px;
        }
    }
 
    //vanishing effect
    &.eltdf-wishlist-vanish-out {
      @include eltdfAnimation(eltdfWishlistvanishOut 1s both);
    }

    @keyframes eltdfWishlistvanishOut {
      0% {
        opacity: 1;
        @include eltdfTransitionTransform(scale(1,1) translate(-50%,-50%));
        -webkit-filter: blur(0px);
        filter: blur(0px);
      }

      100% {
        opacity: 0;
        @include eltdfTransitionTransform(scale(2,2) translate(-50%,-50%));
        -webkit-filter: blur(10px);
        filter: blur(10px);
      }
    }
}

.eltdf-yith-wcqv-holder {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.yith-wcwl-add-to-wishlist {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 12px 0 0 0;
    text-align: center;
}

.yith-wcwl-add-button,
.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    a {
        position: relative;
        display: inline-block;
        vertical-align: top;
        font-size: 0;
        line-height: inherit;
        color: #fff;

        &:before {
            content: "\e089";
            position: relative;
            display: inline-block;
            font-family: "ElegantIcons";
            font-style: normal;
            font-weight: normal;
            font-variant: normal;
            text-transform: none;
            speak: none;
            font-size: 11px;
            line-height: inherit;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            @include eltdfTransition(opacity .2s);
        }

        // content: "W";

        &:not(.eltdf-adding-to-wishlist):hover {
            &:before {
                opacity: 1;
            }
        }

        &.eltdf-adding-to-wishlist {
            @include keyframes(eltdfWishlistLoading) {
                0%, 100% {
                    opacity:1;
                }

                50% {
                    opacity: 0.2;
                }
            }

            &:before {
                @include eltdfAnimation(eltdfWishlistLoading 1s ease infinite);
            }
        }

        &:hover {
            color: #000;
        }
    }
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    a {
        &:before {
            content: "W";
            vertical-align: bottom;
            color: $first-main-color;
        }

        &:hover {
            color: $first-main-color;
        }
    }
}

.yith-wcwl-add-button {
    .ajax-loading {
        display: none !important;
    }
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    span {
        display: none;
    }

    a {

        &:before {
        }
    }
}

.woocommerce-wishlist {

    .wishlist-title{
        display: none;
    }

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        position: relative;
        display: block;
        padding: 20px 30px;
        margin: 50px 0 30px;
        line-height: 60px;
        list-style: none;
        background-color: transparent;
        border: 1px solid $default-border-color;

        > a {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        a {
            float: right;
        }
    }

    table.wishlist_table {
        font-size: inherit;
        margin: 50px 0 100px;

        thead {
            tr {
	            border: none;
                border-bottom: 1px solid $default-border-color;
                font-size: 29px;
                font-weight: 600;
                letter-spacing: 0;
                font-family: $default-heading-font;
                line-height: 1.103em;
                color: $default-heading-color;

                th {
                    border: none;
                    padding: 10px 0;
                    color: #121212;
                    text-align: left;
                    font-size: 21px;
                    font-weight: 600;
                    letter-spacing: 0;
                    font-family: $default-heading-font;
                    line-height: 1.143em;

                    &.product-remove {
                        width: 18px;
                    }
	                
	                &.product-price{
		                text-align: center;
	                }
                }
            }
        }

        tbody {

            tr {
                border: 0;
                border-bottom: 1px solid #e6e6e6;
                
                td {
                    padding: 25px 0;
                    vertical-align: middle;
                    text-align: left;
                    a{
                        font-size: 15px;
                        font-weight: 300;
                        letter-spacing: 0;
                        font-family: $default-text-font;
                        line-height: 1.143em;
                        color: $default-heading-color;
                        text-transform: capitalize;
                    }

                    &.product-remove {
                        text-align: left;

                        a {
                            color: $default-text-color;
                            font-size: 0;
                            position: relative;
                            bottom: 5px;
                            @include phone-landscape{
                                bottom: 4px;
                            }
                            
                            &:before {
                                content: "\f404";
                                display: inline-block;
                                vertical-align: middle;
                                speak: none;
                                color: $default-text-color;
                                font-family: Ionicons;
                                font-size: 25px;
                                line-height: inherit;
                                font-style: normal;
                                font-weight: 400;
                                font-variant: normal;
                                text-transform: none;
                                text-rendering: auto;
                                -webkit-font-smoothing: antialiased;
                                -moz-osx-font-smoothing: grayscale;
                            }

                            &:hover {
                                color: $default-heading-color;
                            }
                        }
                    }

                    &.product-thumbnail {
                        padding-left: 96px;
                        padding-right: 8px;
                        width: 86px;
                        @include ipad-portrait{
                            padding-left: 8px;
                        }

                        a, img {
                            display: block;
                            max-width: 86px;
                        }
                    }

                    &.product-name {

                        .yith-wcqv-button {
                            display: none;
                        }
                    }

                    &.product-price{
                        text-align: center;
                        ins{
                            text-decoration: none;
                        }
                    }

                    &.product-stock-status {

                        span {
                            color: $default-text-color !important;
                        }
                    }

                    &.product-add-to-cart {

                        a {
                            display: inline-block !important;
                            padding: 15px 46px;
                            margin: 0;
                            @include eltdfTransition(color .1s .03s);
                            -webkit-border-radius: 0;
                            -moz-border-radius: 0;
                            border-radius: 0;
                            
                    
                            &:after {
                                content:'';
                                width: 101%;
                                z-index: -1;
                                font-size: 13px;
                                font-weight: 900;
                                text-transform: uppercase;
                                font-family: $default-text-font;
                                border-radius: 0;
                                white-space: nowrap;
                                cursor: pointer;
                                position: relative;
                                overflow: hidden;
                                vertical-align: middle;
                                color: #fff;
                                background-color: $default-button-color;
                                @include eltdfTransition(color .1s .03s);
                            }
                    
                            &:hover {
                                background-color: $default-button-hover;
                    
                                &:after {
                                    @include eltdfTransform(scaleX(0));
                                }
                            }
                        }
                    }
                }
            }
        }

        .yith-wcwl-share{
            margin: 0;
        }
    }
}

.eltdf-single-product-summary {
    .yith-wcwl-add-to-wishlist {
        width: auto;
        background-color: transparent;

        .yith-wcwl-add-button,
        .yith-wcwl-wishlistaddedbrowse,
        .yith-wcwl-wishlistexistsbrowse {
            a {
                font-size: 11px;
                line-height: inherit;
                color: $first-main-color;
                font-weight: 500;
                text-transform: uppercase;
                vertical-align: middle;
                letter-spacing: 2px;
                
                &:hover {
                    color: $first-main-color;
                }


                &:before {
                    content: "\e089";
                    font-size: 14px;
                    padding-right: 1px;
                }
            }
        }

        .yith-wcwl-add-button {
            a {
                &:before {
                    content: "\e089";
                    color: $first-main-color;
                    vertical-align: bottom;
                }
            }
        }
    }
}

.eltdf-wishlist-widget-holder {
    display: inline-block;
    vertical-align: middle;

    a {
        color: $first-main-color;
        font-family: $default-heading-font;
        font-size: 17px;
        font-weight: 700;
        margin: 0 10px;

        .eltdf-wishlist-widget-icon {
            vertical-align: middle;
        }

        .eltdf-wishlist-text {
            font-size: 14px;
            text-transform: uppercase;
        }

        .eltdf-wishlist-items-number {
            color: $default-text-color;
            font-family: $default-text-color;
            font-size: 12px;
        }

        &:hover {
            color: $default-heading-color;
        }
    }
}

.eltdf-light-header {
    .eltdf-wishlist-widget-holder{
        a {

            .eltdf-wishlist-items-number {
                color: inherit;
            }
        }
    }
}

.eltdf-dark-header {
    .eltdf-wishlist-widget-holder{
        a {
            color: $header-dark-color;
            .eltdf-wishlist-items-number {
                color: $header-dark-color;
            }
        }
    }
}

ul.products>.product {

    .yith-wcwl-add-to-wishlist {
        display: none;
    }
}

/* ==========================================================================
   YITH WishList styles - end
   ========================================================================== */