/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.eltdf-pl-holder {
    @include eltdfRelativeHolderLayout();

    .eltdf-pli {
        padding: 0;
        z-index: 100;

	    .eltdf-pli-title {
		    margin: 4px 0 2px;
	    }
	
	    .eltdf-pli-category {
		    margin: 2px 0 2px;
	    }

        .eltdf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .eltdf-pli-rating-holder {
		    @include eltdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .eltdf-pli-rating {
		    @include eltdfWooCommerceRatingsStyle();
	    }

        .eltdf-pli-price {
	        @include eltdfWooCommercePriceStyle();
	        margin: 4px 0 0;
        }
    }

    .eltdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

            .eltdf-pli-text {
                opacity: 1;
            }
        }

        .touch & {
            cursor: pointer;
        }

        .eltdf-pli-image {
            @include eltdfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .eltdf-pli-onsale,
            .eltdf-pli-out-of-stock,
            .eltdf-pli-new-product {
	            @include eltdfWooCommerceMarksGeneralStyle();
            }

            .eltdf-pli-onsale {
	            @include eltdfWooCommerceSaleStyle();
            }

            .eltdf-pli-out-of-stock {
	            @include eltdfWooCommerceOutOfStockStyle();
            }
	
	        .eltdf-pli-new-product {
		        @include eltdfWooCommerceNewProductStyle();
	        }
        }
            
        .eltdf-pli-text {
            @include eltdfAbsoluteHolderLayout();
            padding: 10px;
            opacity: 0;
            box-sizing: border-box;
            @include eltdfTransition(opacity .2s ease-out);
        }

        .eltdf-pli-text-outer {
	        @include eltdfTableLayout();
	        text-align: center;
        }

        .eltdf-pli-text-inner {
            @include eltdfTableCellLayout();

            .eltdf-pli-title,
            .eltdf-pli-category a,
            .eltdf-pli-excerpt,
            .eltdf-pli-rating,
            .eltdf-pli-price {
                color: #fff;
            }
	
	        .eltdf-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .eltdf-pli-add-to-cart {

		        &.eltdf-default-skin {

			        .button,
			        .added_to_cart {
				        @include eltdfButtonSolidColor();
				        background-color: #cca06f!important;
				        
				        &:hover {
				            @include eltdfButtonSolidHoverColor();
					        background-color: #d9ad7c!important;
				        }
			        }
		        }

		        &.eltdf-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
		
		        &.eltdf-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include eltdfButtonDefaultStyle();
	            @include eltdfButtonSize();
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }
        }

	    .eltdf-pli-link {
		    @include eltdfAbsoluteHolderLayout();
	    }
    }

    .eltdf-pli-text-wrapper {
        @include eltdfRelativeHolderLayout();
        margin: 32px 0 0;
	    text-align: center;
	    
	    .eltdf-pli-category{
		    a{
			    font-size: 11px;
			    text-transform: uppercase;
			    color: $first-main-color;
			    font-weight: 500;
			    letter-spacing: 0.12em;
		    }
	    }
    }
	
	&.eltdf-product-info-light {
		
		.eltdf-pli-inner {
			
			.eltdf-pli-text-inner {
				
				.eltdf-pli-title,
				.eltdf-pli-category a,
				.eltdf-pli-excerpt,
				.eltdf-pli-rating,
				.eltdf-pli-price {
					color: #fff;
				}
				
				.eltdf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.eltdf-product-info-dark {
		
		.eltdf-pli-inner {
			
			.eltdf-pli-text-inner {
				
				.eltdf-pli-title,
				.eltdf-pli-category a,
				.eltdf-pli-excerpt,
				.eltdf-pli-rating,
				.eltdf-pli-price {
					color: $default-heading-color;
				}
				
				.eltdf-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	&.eltdf-info-on-image {
		
		.eltdf-pli {
			
			.eltdf-pli-add-to-cart {
				margin: 25px 0 0;
			}
		}
		
		.eltdf-pl-outer{
			.eltdf-pli{
				.eltdf-pli-inner{
					.eltdf-pli-text{
						.eltdf-pli-price{
							color: $first-main-color;
							font-size: 11px;
							font-weight: 500;
							letter-spacing: .12em;
							text-transform: uppercase;
							margin-top: 8px;
						}
					
					    .eltdf-pli-category{
							a{
								color: $first-main-color;
								font-size: 11px;
								text-transform: uppercase;
								font-weight: 400;
							}
						}
					}
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.eltdf-masonry-layout {
		
		.eltdf-pli-inner {
			width: 100%;
			height: 100%;
			
			.eltdf-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/

	/***** Additional Features - begin *****/

	&.eltdf-pl-has-animation {

		&.eltdf-pl-animation-upwards {
			.eltdf-pli {
				pointer-events: none;
				opacity: 0;

				.eltdf-pli-image {
					@include eltdfTransform(scaleY(1.2));
					@include eltdfTransformOrigin(top center);
					will-change: transform;
				}

				.eltdf-pli-text-wrapper {
					opacity: 0;
				}

				&.eltdf-item-show {
					opacity: 1;
					@include eltdfTransition(opacity 1s $aux-easing);

					.eltdf-pli-image {
						@include eltdfTransform(scaleY(1) translateY(0));
						@include eltdfTransitionTransform(.8s cubic-bezier(.165, .84, .44, 1));
					}

					.eltdf-pli-text-wrapper {
						opacity: 1;
						@include eltdfTransition(opacity .4s $aux-easing .4s);
					}

					&.eltdf-item-shown {
						pointer-events: auto;
					}
				}

				.touch & {
					pointer-events: auto;
					opacity: 1;

					.eltdf-pli-text-wrapper,
					.eltdf-pli-image {
						opacity: 1;
						@include eltdfTransform(translateY(0));
					}
				}
			}
		}

		&.eltdf-pl-animation-randomize {
			.eltdf-pli {
				pointer-events: none;
				opacity: 0;

				&.eltdf-item-show {
					opacity: 1;
					@include eltdfTransition(opacity .8s $aux-easing);

					&.eltdf-item-shown {
						pointer-events: auto;
					}
				}

				.touch & {
					pointer-events: auto;
					opacity: 1;
				}
			}
		}
	}

	&.eltdf-portfolio-light{
		article{
			.eltdf-pli-text {
				.eltdf-pli-title{
					color:#fff;
				}
			}
		}
	}

	/***** Additional Features - end *****/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */