/* ==========================================================================
   WooCommerce Checkout page styles - begin
   ========================================================================== */

.eltdf-woocommerce-page {
    
    .woocommerce-form-login-toggle, .woocommerce-form-coupon-toggle{
        background-color: #f8f8f8;
        
        .woocommerce-info{
            border: none;
            
            a{
                padding: 0 !important;
                color: $first-main-color;
                font-weight: 500;
                background-color: transparent;
                border: 0;
                vertical-align: middle;
                text-decoration: underline;
                &:hover{
                    color: #a48562;
                }
            }
        }
    }
    .wc_payment_methods {
        li{
            padding: 14px 38px;
        }
    }
    .checkout_coupon {
        display: block;
        margin: 0 0 34px;
        
        p {
            margin: 0;
        }
    }
    
    &.woocommerce-checkout form.login {
        display: block;
        margin: 0 0 34px;
        
        > *:first-child {
            margin: 0 0 15px;
        }
        
        p {
            margin: 0;
        }
        
        input[type='checkbox'] {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            top: -1px;
            margin: 0 3px 0 5px;
        }
        
        .lost_password {
            display: block;
            margin: 15px 0 0;
        }
    }
    
    .woocommerce-checkout .col2-set:before,
    .woocommerce-checkout .col2-set:after {
        content: '';
        display: table;
    }
    
    .woocommerce-checkout .col2-set:after {
        clear: both;
    }
    
    .woocommerce-checkout {
        .woocommerce-checkout-payment ul li{
            padding: 14px 38px!important;
        }
        .col-1,
        .col-2 {
            position: relative;
            display: inline-block;
            width: 49%;
            margin-right: 1%;
            float: left;
            vertical-align: middle;
            
            h3 {
                margin: 8px 0 25px;
            }
            
            p {
                margin: 0;
            }
            
            label:not(.checkbox) {
                display: block;
                margin: 0 0 5px;
            }
            
            .form-row {
                width: 49%;
                margin-right: 2%;
                float: left;
            }
            
            .form-row-last {
                margin-right: 0;
            }
            
            .form-row-wide,
            .form-row.notes {
                width: 100%;
            }
        }
        
        .col-2 {
            margin-left: 1%;
            margin-right: 0;
        }
        
        .create-account {
            margin: 0 0 15px;
            
            input, label {
                display: inline-block;
                vertical-align: middle;
            }
        }
        
        h3#order_review_heading {
            margin: 31px 0 18px;
        }
        
        table {
            padding: 0;
            margin: 0 0 35px;
            
            tr {
                border: 0;
                border-bottom: 1px solid $default-border-color;
                font-size: 21px;
                font-weight: 600;
                letter-spacing: 0;
                font-family: $default-heading-font;
                line-height: 1.143em;
                color: $default-heading-color;
                
                td{
                    &.product-total{
                        font-size: 15px;
                        font-weight: 300;
                        font-family: $default-text-font;
                        color:$default-text-color;
                    }
                }
                
                &.cart-subtotal {
                    td {
                        font-size: 15px;
                        font-weight: 300;
                        font-family: $default-text-font;
                        color: $default-text-color;
                    }
                }
                
                &.shipping {
                    td{
                            font-size: 15px;
                            font-weight: 300;
                            font-family: $default-text-font;
                            color:$default-text-color;
                    }
                    #shipping_method {
                        list-style: none;
                        margin: 0;
                        
                        li {
                            margin: 3px 0;
                            padding: 0;
                            
                            > * {
                                display: inline-block;
                                vertical-align: middle;
                                margin: 0;
                            }
                        }

                        input {
                            margin-right: 5px;
                        }
                    }
                }
            }
            
            td, th {
                padding: 14px 0;
                text-align: left;
                vertical-align: middle;
                
                &.product-name{
                    font-weight: 700;
                }
            }
            
            th {
                border: 0;
            }
            
            .variation {
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    padding: 0;
                    margin: 0;
                }
                
                p {
                    margin: 0;
                }
            }
        }
        
        .select2-container {
            border-radius: 0;
        }

        @include select2-input-dropdown;
        
        input[type='radio'],
        input[type='checkbox'] {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            top: -1px;
            margin: 0 3px 0 5px;
        }
        
        .woocommerce-checkout-payment {
            display: block;
            margin: 20px 0 70px;

            .woocommerce-privacy-policy-text {
                p {
                    position: relative;
                    margin: 0;
                    font-style: italic;
                    top: -32px;
                }
            }
            
            ul {
                list-style: none;
                margin: 0 0 35px;
                padding: 0;
                
                li {
                    padding: 14px 0;
                    border-bottom: 1px solid $default-border-color;
                    
                    input, label {
                        display: inline-block;
                        vertical-align: middle;

                        img {
                            position: relative;
                            top: 7px;
                        }
                    }
                    
                    &.payment_method_paypal {
                        
                        label {
                            
                            img {
                                vertical-align: middle;
                                margin: 0 8px;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Checkout page styles - end
   ========================================================================== */