/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    $thumb: 80px;
    $inner-padding: 15px; // distance between image and content
    $widget-list-item-padding: 15px; // same as sidebar vars

    clear: both;

    li {
        position: relative;
    }
    
    .star-rating{
        display: none;
    }
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // title

    .product-title {
        @include eltdfH5();
        font-size: 21px;
        line-height: 24px;
        letter-spacing: 0;
        color: $default-heading-color;
        font-weight: 600;
        font-family: $default-heading-font;
        margin-top: 17px;
        margin-bottom: 4px;
    }
    
    .woocommerce-Price-amount{
        font-size: 11px;
        text-transform: uppercase;
        font-weight: 500;
        letter-spacing: 0.12em;
        color: $first-main-color!important;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // star rating

    li:not(.wc-layered-nav-rating) {
        .star-rating {
            margin-bottom: 7px;
            margin-top: 7px;

            span {
                max-width: 100px;
            }
        }
    }

    li.wc-layered-nav-rating {
        a {
            text-decoration: none;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price

    .amount {
        color: inherit;

        span { // currency symbol
            color: inherit;
        }
    }

    del,
    ins {
        display: block;

        // currency symbol
        span {
            color: inherit;
        }
    }

    ins {
        text-decoration: none;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // layered nav

    &.widget_layered_nav {
        .chosen {
            a {
                color: $first-main-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price filter

    &.widget_price_filter {
        form {
            margin-top: 31px;
        }

        .price_slider_wrapper {
            .ui-widget-content {
                background-color: #c8c8c8;
                height: 2px;
                position: relative;

                .ui-slider-handle {
                    background-color: #040a1f;
                    cursor: pointer;
                    height: 18px;
                    margin: 0 0 0 -1px;
                    outline: medium none;
                    position: absolute;
                    top: -8px;
                    width: 2px;
                    z-index: 2;

                    &:last-child {
                        @include eltdfTransform(translateX(-50%));
                    }
                }

                .ui-slider-range {
                    background-color: #040a1f;
                    display: block;
                    height: 2px;
                    position: absolute;
                    top: 0;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 41px 0 0;

            .button {
                float: left;
                padding: 12px 24px;
                font-weight: 500;

                @include eltdfButtonSolidColor();

                &:hover {
                    @include eltdfButtonSolidHoverColor();
                }
            }

            .price_label {
                float: right;
                line-height: 50px; // to match button height
                padding-right: 15px;
                @include laptop-landscape-large{
                    padding-right: 0;
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images

    &.widget_shopping_cart,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
        ul {
            li {
                min-height: $thumb;
                position: relative;

                a {
                    img {
                        left: 0;
                        max-width: $thumb;
                        position: absolute;
                        width: 100%;
                    }

                    span {
                        box-sizing: border-box;
                        display: block;
                        padding: 0 0 0 #{$thumb + $inner-padding};
                        position: relative;
                    }
                }

                > *:not(a) {
                    clear: both;
                    float: left;
                    margin: 0 0 0 #{$thumb + $inner-padding};
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images and plain text next to image

    &.widget_shopping_cart {
        a {
            display: block;
            padding: 0 0 0 #{$thumb + $inner-padding};

            @include eltdfH5();
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // recent reviews

    //&.widget_recent_reviews {
    //    .reviewer {
    //    }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // shopping cart

    &.widget_shopping_cart {
        .remove {
            font-size: 0;
            font-weight: normal;
            line-height: 1;
            position: absolute;
            right: 0;
            top: 5px;
            text-decoration: none;

            &:after {
                content: "\4d";
                display: block;
                font-family: 'ElegantIcons';
                font-size: 15px;
                line-height: 15px;
                position: relative;
            }
        }

        .variation {
            @include eltdfRelativeHolderLayout();

            > * {
                display: inline-block;
                margin: 0;
                vertical-align: middle;

                p {
                    margin: 0;
                }
            }
        }

        .total {
            margin: 17px 0 13px;
        }

        .buttons {
            .button {
                @include eltdfButtonDefaultStyle();
                @include eltdfButtonSize();
                @include eltdfButtonSolidColor();

                &:hover {
                    @include eltdfButtonSolidHoverColor();
                }

                &:first-child {
                    float: left;
                }

                &:last-child {
                    float: right;
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */