/* ==========================================================================
Product Categories shortcode styles - begin
========================================================================== */

.eltdf-product-categories-holder {

    .eltdf-product-category {
        float: left;
        position: relative;
        box-sizing: border-box;
        @include eltdfTransition(opacity .2 ease-in-out);

        .eltdf-product-category-inner{
            @include eltdfRelativeHolderLayout();
            height: 100%;
			background-color: rgba($first-main-color, 0.1);
            min-height: 200px;
            box-sizing: border-box;

			.eltdf-product-category-img-wrapper{
                display: block;
                overflow: hidden;
			}

			.eltdf-link{
				@include eltdfAbsoluteHolderLayout();
                z-index: 50;
                background-color: rgba($first-main-color, .3);
                opacity: 0;
				@include eltdfTransition(opacity .4s $default-easing);
			}

          &:hover {

            .eltdf-product-category-content {
                //opacity: 1;
            }
          }
          
        }

        .eltdf-product-category-content {
          position: absolute;
          top: 67%;
          left: 0;
          width: 100%;
          z-index: 150;
          //@include eltdfTransform(translateY(-50%));
          text-align: left;

           &.top{
               position: absolute;
               top:0;
               left:0;
           }

           &.bottom{
               position: relative;
               margin: 24px 0 0 0;
               text-align: center;
            }

            .eltdf-category-title{
                margin: 6px 43px;
            }
            
            .eltdf-btn-text{
                color: $first-main-color;
            }
            
            a{
                padding: 0 43px;
            }
            
        }

        &.eltdf-category-with-image{
            text-align: center;
            
        	img{
                display: block;
                //@include eltdfFlickerFix();
                @include eltdfTransitionTransform(4s cubic-bezier(.165, .84, .44, 1));
        	}

            .eltdf-product-category-content {

                &.top{
                    top: 23px;
                    left: 25px;
                }
            }

            &:hover {
                img {
					@include eltdfTransform(scale(1.06));
                }
            }
        }
		&:nth-child(3n + 2){
			.eltdf-product-category-inner{
				background-color: rgba($first-main-color, 0.05);
			}
		}
		&:nth-child(3n + 3){
			.eltdf-product-category-inner{
				background-color: rgba($first-main-color, 0.1);
			}
		}
    
        &:nth-child(2n){
            .eltdf-product-category-content{
                display: none;
            }
        }

    }
    
    @supports (-ms-ime-align: auto) {
        .eltdf-product-category{
            clear: none!important;
        }
    }
    
    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
        .eltdf-product-category{
            clear: none!important;
        }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'large');
$columns_space_width: (0, 5, 10, 15, 25);

@for $i from 0 to length($columns_space_label) {
&.eltdf-#{nth($columns_space_label,$i+1)}-space {
    $column_width: nth($columns_space_width,$i+1);
    $column_width_padding: $column_width * 2;

    .eltdf-product-categories-holder-inner {
        @if($column_width == 0) {
            margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }

            .eltdf-product-category{
                padding: 0 #{$column_width}px;
                margin: 0 0 #{2*$column_width}px;
            }
        }
    }
}

/*************** Standard Layout - begin ***************/

$standard_list_columns: ('two', 'three', 'four', 'five', 'six');

@for $i from 0 to length($standard_list_columns) {
    &.eltdf-#{nth($standard_list_columns,$i+1)}-columns {
        $column_width: 100%/($i+2);

        .eltdf-product-category{
            width: $column_width;

            @media only screen and (min-width: $phone-landscape-plus-pixel) {

                &:nth-child(#{$i+2}n+1) {
                    clear: both;
                }
            }
        }
    }
}

/*************** Standard Layout - end ***************/
