/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .eltdf-content,
div.woocommerce {

    .eltdf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .eltdf-quantity-input,
        .eltdf-quantity-minus,
        .eltdf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            text-align: center;
            -webkit-user-select: none;
            box-sizing: border-box;
        }

        .eltdf-quantity-input {
            position: relative;
            height: 58px;
            width: 58px;
            padding: 0;
            margin: 0 28px 0 0;
            font-size: 15px;
            font-weight: 300;
            font-family: inherit;
            line-height: 56px;
            outline: 0;
            color: $default-heading-color;
        }

        .eltdf-quantity-minus,
        .eltdf-quantity-plus {
            position: absolute;
            right: 0;
            width: 28px;
            height: 29px;
            line-height: 26px;
            border-left: 0;
            color: $default-heading-color;
            cursor: pointer;
            @include eltdfTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }

        .eltdf-quantity-plus {
            top: 0;
            border-bottom: 0;
            
            &:before{
                content: "\e083";
                font-family: "linea-arrows-10";
                display: inline-block;
                font-size: 20px;
            }
        }

        .eltdf-quantity-minus {
            bottom: 0;
    
            &:before{
                content: "\e019";
                font-family: "linea-arrows-10";
                display: inline-block;
                font-size: 20px;
                position: relative;
                top: 1px;
            }
        }
    }
}

.woocommerce-page .eltdf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
            padding: 12px 26px;
            line-height: 2.5em;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */