/* ==========================================================================
   WooCommerce Cart page styles - begin
   ========================================================================== */

.eltdf-woocommerce-page {
	
	table.cart {
		margin-top: 39px;
		
		thead {
			
			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;
				
				th {
					padding: 10px 0;
					color: $default-heading-color;
					text-align: left;
					font-size: 21px;
					font-weight: 600;
					letter-spacing: 0;
					font-family: $default-heading-font;
					line-height: 1.143em;
				}
			}
		}
		
		tbody {
			
			tr {
				border: 0;
			}
		}
		
		tr.cart_item {
			border-bottom: 1px solid $default-border-color;
			
			td {
				padding: 25px 0;
				vertical-align: middle;
				text-align: inherit;
				
				&.product-remove {
					padding-left: 10px;
					padding-right: 10px;
					
					a {
						color: $default-text-color;
						font-size: 0;
						line-height: 2;
						
						&:after {
							content: "\f404";
							display: inline-block;
							vertical-align: middle;
							speak: none;
							color: inherit;
							font-family: "Ionicons";
							font-size: 25px;
							line-height: inherit;
							font-style: normal;
							font-weight: normal;
							font-variant: normal;
							text-transform: none;
							text-rendering: auto;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
						}
						
						&:hover {
							color: $default-heading-color;
						}
					}
				}
				
				&.product-thumbnail {
					width: 86px;
					padding-right: 10px;
					
					a, img {
						display: block;
						width: 100%;
					}
				}
				
				.variation {
					
					> * {
						display: inline-block;
						vertical-align: middle;
						padding: 0;
						margin: 0;
					}
					
					p {
						margin: 0;
					}
				}
			}
		}
		
		td.actions {
			padding: 35px 0 36px;
			text-align: inherit;
			
			.coupon {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				float: left;

				.button {
					bottom: 1px;
				}
				
				label {
					display: none;
				}
			}
			
			> input {
				float: right;
			}

			.button {
				margin: 0 0 0 22px;
			}
		}
	}
	
	.cart-collaterals {
		@include eltdfRelativeHolderLayout;
		margin: 0 0 40px;
		
		h2 {
			position: relative;
			display: block;
			margin: 0 0 26px;
		}
		
		table {
			margin: 0 0 35px;
			
			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;
			}
			
			td, th {
				padding: 14px 0;
				text-align: left;
				vertical-align: middle;

				.woocommerce-shipping-destination {
					strong {
						font-weight: 300;
					}
				}
			}
			
			th {
				font-size: 21px;
				font-weight: 600;
				letter-spacing: 0;
				font-family: $default-heading-font;
				line-height: 1.143em;
				color: $default-heading-color;
				width: 20%;
				padding-right: 20px;
				border: 0;
			}
		}
		
		tr.shipping {
			
			#shipping_method {
				list-style: none;
				margin: 0 0 7px;
				
				li {
					margin: 3px 0;
					padding: 0;
					
					> * {
						display: inline-block;
						vertical-align: middle;
						margin: 0;
					}

					input {
						margin-right: 5px;
					}
				}
			}
		}
		
		.woocommerce-shipping-calculator {

			@include select2-input-dropdown;
			
			.shipping-calculator-button {
				margin: 0 0 10px;
				display: inline-block;
				vertical-align: middle;
				text-decoration: underline;
				padding: 0 !important;
				color: $first-main-color;
				font-weight: 500;
				background-color: transparent;
				border: 0;
			}
			
			p {
				margin: 0;
			}
		}
	}
	
	.cross-sells {
		margin: 30px 0 0;
		border-top: 1px solid $default-border-color;
		
		> h2 {
			display: block;
			margin: 36px 0 50px;
			text-align: center;
		}
	}
	
	.cart_totals {
		margin: 1px 0 20px;
	}
}

.eltdf-woocommerce-page {
	
	.cart-empty {
		@include eltdfDefaultHeadingStyle();
		@include eltdfH2();
		font-size: 49px;
		font-weight: 600;
		letter-spacing: 0;
		font-family: $default-heading-font;
		line-height: 1.061em;
		color: $default-heading-color;
		text-align: center;
		margin: 40px 0 33px;
	}
	
	.return-to-shop {
		position: relative;
		display: block;
		text-align: center;
		margin: 0 0 60px;
	}
}
/* ==========================================================================
   WooCommerce Cart page styles - end
   ========================================================================== */