/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.eltdf-woo-single-page {
	
	&.eltdf-woo-single-thumb-on-left-side {
		
		.eltdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}
					
					$thumb_size: 100;
					$space_between_items: 20;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;
							
							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: #{$thumb_size}px;
								
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
						}
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4', 'columns-5');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include eltdfRelativeHolderLayout();
	    float: none;
	    margin: 0;
    }
        
    .eltdf-single-product-content {
        @include eltdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 55%;
		    margin: 0;
		    padding: 0 29px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include eltdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .eltdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 45%;
        padding: 0 0 0 10px;
        float: left;
        box-sizing: border-box;
	
	    .eltdf-single-product-title {
		    display: block;
		    margin: 0 0 5px;
	    }
	
	    .price {
		    @include eltdfWooCommercePriceStyle();
	    }
	
	    .woocommerce-product-rating {
		    @include eltdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 11px 0;
		    
		    .woocommerce-review-link{
			    display: none;
		    }
	    }
	
	    .woocommerce-product-details__short-description {
		    @include eltdfRelativeHolderLayout();
		    margin: 26px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 30px 0 0;
		
		    > span {
			    display: block;
			    margin: 3px 0 10px;
			    font-family: $default-heading-font;
			    font-size: 19px;
			    font-weight: 600;
			    letter-spacing: 0;
			    line-height: 22px;
			    color: $default-heading-color;
			
			    a, .sku {
				    color: $default-text-color;
				    font-size: 15px;
				    font-weight: 300;
				    font-family: $default-text-font;
				    line-height: 28px;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
			    
			    .eltdf-product-categories-separator{
				    color: $default-text-color;
			    }
			    
			    a{
				    &:first-child{
					    margin-left: 3px;
				    }
			    }
			    
			    .sku{
				    margin-left: 3px;
			    }
		    }
	    }
	
	    .eltdf-woo-social-share-holder {
		    @include eltdfRelativeHolderLayout();
		    margin: 18px 0 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
		    
		    p{
			    margin: 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include eltdfRelativeHolderLayout();
		    margin: 41px 0 0;
	    }
	
	    .eltdf-quantity-buttons {
		    margin: 0 16px 0 0;
	    }
	    
	    .single_add_to_cart_button {
		    padding: 16px 36px!important;
		    &:hover{
			    background-color: #d9ad7c;
		    }
	    }
    }
	
	.woocommerce-tabs {
		@include eltdfRelativeHolderLayout();
		margin: 68px 0 0;
		
		ul.tabs {
			@include eltdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			
			text-align: center;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				background-color: $default-button-color;
				margin: 0 4px;
				
				a {
					font-size: 12px;
					font-weight: 400;
					text-transform: uppercase;
					letter-spacing: 0.12em;
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 13px 29px;
					color: #fff;
				}
				
				&.active{
					background-color: $default-button-hover;
				}
				
				&:hover{
					background-color: $default-button-hover;
				}
			}
		}
		
		.entry-content {
			@include eltdfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 28px 150px 20px;
				text-align: center;
			}
		}
		
		table {
			margin: 25px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
				font-family: $default-heading-font;
				font-size: 19px;
				font-weight: 600;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include eltdfRelativeHolderLayout();
			margin: 18px 0 20px;
			
			h2 {
				font-size: 29px;
				font-weight: 600;
				line-height: 24px;
				margin: 11px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include eltdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					position: relative;
					top: -10px;
					padding: 0 0 0 90px;
					
					.woocommerce-review__author, .woocommerce-review__dash, .woocommerce-review__published-date{
						color: $default-heading-color;
					}
					
					p {
						margin: 0;
						
						.woocommerce-review__author, .woocommerce-review__dash, .woocommerce-review__published-date{
							font-size: 21px;
							font-weight: 600;
							font-family: $default-heading-font;
						}
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
					font-size: 29px;
					font-weight: 600;
					letter-spacing: 0;
					font-family: $default-heading-font;
					line-height: 1.103em;
					color: $default-heading-color;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					@supports (-ms-ime-align: auto) {
						vertical-align: middle;
					}
					@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
						vertical-align: middle;
					}
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include eltdfWooCommerceSingleProductRatingsStyle();
					}
				}
				
				.form-submit{
					.submit{
						-webkit-border-radius: 0!important;
						-moz-border-radius: 0!important;
						border-radius: 0!important;
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 35px 0 0;
		
		> h2 {
			font-size: 39px;
			display: block;
			margin: 26px 0 42px;
			text-align: left;
			color: #010101;
		}
		
		.product {
			
			.eltdf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */